
import { Component, Vue,Watch } from 'vue-property-decorator';
import ActionBlock from '@/components/search/actionBlock.vue';
import ApplyStrategy from '@/views/alarm/apply_strategy.vue';
import Detail from '@/views/alarm/strategy_detail.vue'
import Service from '@/https/alarm/list'
import CommonDel from '@/views/alarm/commonDel.vue';
import ApplyDetail from '@/views/alarm/apply_product_detail.vue'
import moment from 'moment';
import {Table} from 'element-ui'
import {rightClick} from "@/utils/vmOp2/rightClick";
import RightClick from "@/views/vmOp2/component/right-click.vue";
import {hideMenu} from "@/utils/vmOp2/hideMenu";
@Component({
  components:{
    RightClick,
    ActionBlock,
    ApplyStrategy,
    CommonDel,
    ApplyDetail,
    Detail
  }
})
export default class Strategy extends Vue{
  $router;
  $route;
  private search:any={
    name:{placeholder:'请输入策略名称'}
  }
  private list=[]
  private drawer:Boolean=false
  private search_data:any={}
  private current:number = 1
  private size:number = 20
  private total:number = 0
  private moment:any = moment;
  private del_visible:boolean=false;
  private detail_visible:Boolean=false
  private strategy_detail:Boolean=false
  private strategy_rows:any=[]
  private enable:Boolean=true;
  private auth_list:any=[]
  private menus= [
    {label: '修改',value: 'edit',single:true,disabled:false},
    {label: '详情',value: 'detail',single:true,disabled:false},
    {label: '应用',value: 'apply',disabled:false},
    {label: '停用',value: 'stop',disabled:false},
    {label: '删除',value: 'delete',disabled:false},
  ]
  created() {
    this.fn_search()
    this.auth_list=this.$store.state.auth_info['alarm_strategy']
  }
  @Watch('strategy_rows',{immediate:true,deep:true})
  private watch_multi(n){
    if(n.length>0){
      this.handleMenus()
    }
  }
  @Watch("drawer")
  private watch_drawer(newVal){
    this.clear_info(newVal)
  }
  @Watch("del_visible")
  private watch_del_visible(newVal){
    this.clear_info(newVal)
  }
  @Watch("strategy_detail")
  private watch_strategy_detail(newVal){
    this.clear_info(newVal)
  }
  private handleMenus(){
    this.menus.forEach(item => {
      item.disabled = this.judge(item.value);
      if (item.value === 'apply') {
        item.label = this.strategy_rows.length >= 1 ?  '应用' : this.strategy_rows[0].enable ? '应用详情' : '应用'
      }
    });
  }
  private judge(value) {
    let flag = false;

    switch (value) {
      case 'edit':
      case 'delete':
        flag = this.strategy_rows.every(item => item.enable) || !this.auth_list.includes(value);
        break;
      case 'stop':
        flag = !this.strategy_rows.every(item => item.enable) || !this.auth_list.includes(value);
        break;
      case 'detail':
      case 'apply':
        flag = !this.auth_list.includes(value);
        break;
      default:
        console.warn(`Unknown value passed to judge: ${value}`);
        return false;
    }

    return flag;
  }
  private handle(item) {
    const { label, value } = item;
    const singleRow = this.strategy_rows.length === 1;
    const row = singleRow ? this.strategy_rows[0] : null;

    const actions = {
      edit: () => this.edit(row.id),
      detail: () => this.detail(row),
      apply: () => this.apply(true, row),
      stop: () => this.apply(false, row),
      delete: () => this.del(row)
    };
    const batchActions = {
      apply: () => this.apply(true,''),
      stop: () => this.apply(false,''),
      delete: () => this.del(false)
    };
    if (singleRow) {
      if (actions[value]) {
        actions[value]();
      }
    } else {
      if (batchActions[value]) {
        batchActions[value]();
      }
    }
    hideMenu();
  }

//改变点击行得选中状态
  private FnOperRow(row){
    (this.$refs.strategy_table as any).toggleRowSelection(row)
  }
  //改变选中行的背景颜色
  rowStyle({row}) {
    const isSelected = this.strategy_rows.some(item => item.id === row.id);
    if (isSelected) {
      return 'rowStyle'
    }
  }
  //右键弹出操作
  FnRightClick(row,column,event){
    //判断当前行是否被选中，没选中时需选中并弹出菜单
    const isSelected = this.strategy_rows.some(item => item.id === row.id);
    if (!isSelected) {
      (this.$refs.strategy_table as any).toggleRowSelection(row)
    }
    rightClick(row,column,event)
  }
  private clear_info(newVal){
    if(!newVal){
      this.strategy_rows=[]
      this.toggleSelection()
    }
  }
  private toggleSelection() {
    const table = this.$refs.strategy_table as Table
    table.clearSelection()
  }
  private fn_search(data:any={}){
    this.current =1
    this.search_data = data
    this.getStrategyList()
  }
  private async getStrategyList(){
    const {search_data:{name}} = this
    let res:any = await Service.get_strategy_list({
      name,
      page:this.current,
      pageSize:this.size,
    })
    if(res.code==='Success'){
      this.list = res.data.datas || []
      this.list.map(item=>{
        item = Object.assign(item,{instances_name:item.instances.map(inn=>inn.instance_name)})
        return item
      })
      this.total = res.data.total || 0
    }

  }
  private handleSizeChange(size){
    this.size = size
    this.getStrategyList()
  }
  private handleCurrentChange(cur){
    this.current = cur
    this.getStrategyList()
  }
  private handleSelectionChange(val){
    this.strategy_rows = val
  }
  private create(){
    this.$router.push('/alarm_strategy/create')
  }
  private go_detail(row){
    this.strategy_rows=[row]
    this.detail_visible=true
  }
  private edit(id:string){
    this.$router.push({path:'/alarm_strategy/create',query:{id}})
  }
  private detail(row){
    this.strategy_rows=[row]
    this.strategy_detail=true
  }
  private del(ids){
    if(!ids && this.strategy_rows.length===0){
      this.$message.warning("请先勾选策略！")
      return;
    }
    if(!this.strategy_rows.every(item=>!item.enable)){
      this.$message.warning("已启用的策略不能被删除！")
      return;
    }
    if(ids){
      this.strategy_rows=[ids]
    }
    this.del_visible=true
  }
  private async stop(){
    const temp:any=[]
    this.strategy_rows.map(item=>{
      const {az,callbackURL,contactGroupIDs,customerID,customer_name,regions,silentPeriod,effectStartTime,effectEndTime,enable,id,name} =item
      let obj={
        az,
        callbackURL,
        contactGroupIDs,
        customerID,
        customer_name,
        regions,
        silentPeriod,
        effectStartTime,
        effectEndTime,
        enable:!enable,
        id,
        name
      }
      temp.push(obj)
      return item;
    })
    let res:any = await Service.apply_strategy({
      data:temp
    })
    if(res.code==='Success'){
      this.$message.warning("停用策略任务下发成功！")
      this.strategy_rows=[]
      this.getStrategyList()
    }
  }
  private apply(enable:Boolean,row:any){
    if(row) this.strategy_rows=[row];
    if(!row && this.strategy_rows.length===0){
      this.$message.warning("请先勾选策略！")
      return;
    }
    if(!row && !this.strategy_rows.every(item=>item.enable===!enable)){
      this.$message.warning(`只允许对${enable ? '未应用' :'已应用'}的策略执行批量操作！`)
      return;
    }
    if(!enable){
      const names = this.strategy_rows.map(item=>item.name);
      const h = this.$createElement;
      this.$msgbox({
        title: '提示',
        message: h('p', null, [
          h('span', null, '确定停用 '),
          h('i', { style: 'color: #455cc6' }, `${names.join(',')}`),
          h('span', null, '  这些策略吗, 是否继续?'),
        ]),
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        iconClass: 'el-icon-warning',
      }).then(() => {
        this.stop()
      }).catch(()=>{
        this.$message({
          type: 'info',
          message: '已取消停用'
        });
        this.strategy_rows=[]
        this.toggleSelection()

      })
    }else{
      this.drawer=true
      this.enable = enable
    }

  }
  private close_apply(val){
    this.strategy_rows=[]
    val==='1' && this.getStrategyList()
  }

}
